import React, { useState, useRef } from "react";
import { StyledMainPage } from "./MainPage.styled";
import {
  withGoogleMap,
  GoogleMap,
  Marker,
  Polyline,
  Circle,
  Rectangle,
  Polygon,
} from "react-google-maps";
import { DrawingManager } from "react-google-maps/lib/components/drawing/DrawingManager";
import { useEffect } from "react";
const google = window.google;
let _map;
const pepeData = [
  {
    id: "001",
    name: "Copium",
    icon: "https://cdn3.emoji.gg/emojis/2643-copium.png",
    price: 10,
  },
  {
    id: "002",
    name: "Pepeyes",
    icon: "https://cdn3.emoji.gg/emojis/7572-pepe-yes.png",
    price: 20,
  },
];

const MainPage = () => {
  const mapRef = useRef();
  const MAP_API_KEY = process.env.REACT_APP_MAP_API_KEY;
  const [pokemonMarker, setPokemonMarker] = useState([]);
  const [drawingControlEnabled] = useState(true);
  const [marker, setMarker] = useState(null);
  const [polyline, setPolyline] = useState(null);
  const [circleRadius, setCircleRadius] = useState(null);
  const [circleCenter, setCircleCenter] = useState(null);
  const [rectangle, setRectangle] = useState(null);
  const [polygon, setPolygon] = useState(null);
  const [visible] = useState(true);
  const [shapeItems, setShapeItems] = useState([]);

  const overlay = (e) => {
    console.log(e);
    shapeItems.push(e);
    setShapeItems([...shapeItems]);
    switch (e.type) {
      case "marker":
        setMarker({
          lat: e.overlay.getPosition().lat(),
          lng: e.overlay.getPosition().lng(),
        });
        break;
      case "polyline":
        setPolyline(e.overlay.getPath());
        break;
      case "circle":
        setCircleCenter(e.overlay.getCenter());
        setCircleRadius(e.overlay.getRadius());
        break;
      case "rectangle":
        setRectangle(e.overlay.getBounds());
        break;
      case "polygon":
        setPolygon(e.overlay.getPaths());
        break;
      default:
        break;
    }
  };

  const drag = (e, pokemon) => {
    console.log("e", e);
    const { offsetLeft } = mapRef.current;

    const x = e.pageX - offsetLeft;
    const y = e.pageY;
    if (x > 0) {
      const point = new google.maps.Point(x, y);
      console.log("point", point, google.maps);
      console.log(_map);
      // const position = google.maps.getProjection.fromLatLngToPoint(point);
      const position = point2LatLng(point, _map);
      console.log("position", position);
      var iconWidth = 32;
      var iconHeight = 32;
      const marker = new google.maps.Marker({
        position: new google.maps.LatLng(position.lat(), position.lng()),
        draggable: true,
        icon: {
          url: pokemon.icon,
          size: google.maps.Size(1, 1),
          target: google.maps.Point(iconWidth / 2, iconHeight / 2),
          origin: google.maps.Point(iconWidth / 2, iconHeight / 2),
          scaledSize: new google.maps.Size(32, 32),
        },
        title: pokemon.name,
      });

      setPokemonMarker([...pokemonMarker, { marker, price: pokemon.price }]);
    }
  };

  const point2LatLng = (point, map) => {
    var topRight = map
      .getProjection()
      .fromLatLngToPoint(map.getBounds().getNorthEast());
    var bottomLeft = map
      .getProjection()
      .fromLatLngToPoint(map.getBounds().getSouthWest());
    var scale = Math.pow(2, map.getZoom());
    var worldPoint = new google.maps.Point(
      point.x / scale + bottomLeft.x,
      point.y / scale + topRight.y
    );
    return map.getProjection().fromPointToLatLng(worldPoint);
  };

  useEffect(() => {
    console.log("pokemonMarker", pokemonMarker);
  }, [pokemonMarker]);

  const GoogleMapCustom = withGoogleMap((props) => {
    return (
      <GoogleMap
        ref={(map) => (_map = map)}
        defaultCenter={{ lat: 13.7223496, lng: 100.5804235 }}
        defaultZoom={17}
      >
        <DrawingManager
          onOverlayComplete={overlay}
          defaultOptions={{
            drawingControlOptions: {
              position: google.maps.ControlPosition.TOP_CENTER,
              drawingModes: [
                google.maps.drawing.OverlayType.CIRCLE,
                google.maps.drawing.OverlayType.POLYGON,
                google.maps.drawing.OverlayType.POLYLINE,
                google.maps.drawing.OverlayType.RECTANGLE,
              ],
            },
          }}
          options={{
            drawingControl: drawingControlEnabled,
          }}
        />

        {pokemonMarker.length &&
          pokemonMarker.map((pokemon, index) => {
            console.log("poke", pokemon);
            return (
              <Marker
                key={index}
                icon={pokemon.marker.icon}
                position={pokemon.marker.position}
                draggable={true}
              />
            );
          })}

        {marker !== null && <Marker position={marker} draggable={true} />}

        {polyline !== null && <Polyline path={polyline} draggable={true} />}
        {circleRadius !== null && (
          <Circle
            radius={circleRadius}
            center={circleCenter}
            visible={visible}
            draggable={true}
          />
        )}

        {rectangle !== null && (
          <Rectangle bounds={rectangle} draggable={true} />
        )}
        {polygon !== null && <Polygon paths={polygon} draggable={true} />}
      </GoogleMap>
    );
  });
  return (
    <div>
      <StyledMainPage>
        <div style={{ flexGrow: "1" }}>
          <ul>
            {pepeData.map((pokemon) => (
              <li key={pokemon.id}>
                <img
                  draggable
                  width="32"
                  height="32"
                  src={pokemon.icon}
                  alt={pokemon.name}
                  onDragEnd={(e) => drag(e, pokemon)}
                />{" "}
                = {pokemon.price} $
              </li>
            ))}
          </ul>
          {pokemonMarker.length &&
            pokemonMarker.reduce((previousValue, currentValue) => {
              return previousValue + Number(currentValue.price);
            }, 0)}{" "}
          $
        </div>

        <GoogleMapCustom
          googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${MAP_API_KEY}&libraries=drawing`}
          loadingElement={<div style={{ height: `100vh` }} />}
          containerElement={
            <div ref={mapRef} style={{ height: `500px`, flexGrow: "3" }} />
          }
          mapElement={<div style={{ height: `100%` }} />}
        />
      </StyledMainPage>
    </div>
  );
};

export default MainPage;
